<template>
    <div class="enemy-wrapper">
        <div class="enemy">
            <h2 class="enemy-title" v-if="enemyList[monster].type === 1">
                БОСС!
            </h2>
            <h3 class="enemy-name">
                {{enemyList[monster].name}}
            </h3>
        </div>
        <div class="enemy-health">
            {{health}}
        </div>
    </div>
    <button @click="inflictDamage(multiplyHealth)" class="main-button">
        Ударить
    </button>
</template>

<script>
    import {ref} from "vue";

    export default {
        name: "Enemy",
        setup() {
            const rnd = (min,max) => {
                return Math.floor(Math.random() * (max - min + 1)) + min;
            }
            let enemyList = ref([{name: 'Мега-кроль', type: 0},
                {name: 'Бешенная белка', type: 0},
                {name: 'Злобный карасик', type: 0},
                {name: "Большой енот", type: 0},
                {name: "Лось", type: 0}])
            let enemyListLength = enemyList.value.length-1
            let health = ref(10)
            let staticHealth = ref(10)
            let monster = rnd(0,enemyListLength)
            let killedMonster = 0
            let multiplyHealth = 1.1
            return {
                enemyList,
                enemyListLength,
                health,
                monster,
                staticHealth,
                rnd,
                killedMonster,
                multiplyHealth
            }
        },
        methods: {
            updateMonster() {
                this.killedMonster++
                this.$store.commit('killingEnemy')
                if(this.killedMonster === 20) {
                    this.enemyList = [
                            {name: 'Царь мелких животных - Кот Барсик', type: 1}
                        ]
                    this.staticHealth = 100
                    this.health = 100
                }
                if(this.killedMonster === 21) {
                    this.$store.commit('addMoney', 10)
                }
                if(this.killedMonster > 20) {
                    this.enemyList = [{name: 'Большая черепаха', type: 0},
                        {name: 'Саша-Камень', type: 0},
                        {name: "Сбежавший пельмень", type: 0},
                        {name: "Злая лиса", type: 0},
                        {name: "Волк-Дрищ", type: 0}]
                }
                if(this.killedMonster === 40) {
                    this.enemyList = [
                            {name: 'Волк-Не-Дрищ', type: 1}
                        ]
                    this.staticHealth = 1000
                    this.health = 1000
                }
                if(this.killedMonster === 41) {
                    this.$store.commit('addMoney', 25)
                }
                if(this.killedMonster > 40) {
                    this.enemyList = [
                        {name: 'Очень большая черепаха', type: 0},
                        {name: 'Волк обыкновенный, ничем не примечательный', type: 0},
                        {name: "Сбежавшая кастрюля с пельменями", type: 0},
                        {name: "Кулич", type: 0},
                        {name: "Щучка-Сучка", type: 0}]
                    this.$store.commit('addMoney', 2)
                }

                if(this.killedMonster === 60) {
                    this.enemyList = [
                            {name: 'БОЛЬШАЯ ЧЕРЕПАШКА ПО ИМЕНИ НАТАШКА', type: 1}
                        ]
                    this.staticHealth = 10000
                    this.health = 10000
                    this.$store.commit('addMoney', 75)
                }
                this.monster = this.rnd(0,this.enemyList.length-1)
            },
            inflictDamage(indexOfMultiply) {
                this.health = this.health - this.$store.state.clickPower

                if(this.health <= 0) {
                    this.staticHealth = this.staticHealth * indexOfMultiply
                    let updatedHealth = Math.round(this.staticHealth)
                    this.health = updatedHealth
                    this.updateMonster()
                }
            },
        }
    }
</script>

<style scoped>
    .enemy{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .enemy-title{
        color: red;
        font-weight: 700;
        padding: 0 0 0 40px;
    }
    .enemy-wrapper{
        display: flex;
        align-items: flex-end;
        justify-content: center;
    }
    .enemy-health{
        margin: 0 0 18px 10px;
        font-size: 17px;
    }
    .main-button{
        padding: 15px 25px;
        background: rgba(135,206,250,0.7);
        cursor: pointer;
        border: none;
        border-radius: 5px;
        color: #fff;
        margin: 0 0 15px 0;
        outline: none;
    }
    .main-button::after{
        content: url("../assets/punch-icon.png");
        position: relative;
        top: 3px;
    }
    .main-button:active{
        background: rgba(135,206,250,1);
    }
</style>